import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetMemberDirectorySettingsRequest, GetMemberDirectorySettingsResponse, IMemberDirectorySettingsService, PostMemberDirectorySettingsRequest } from '../@intermediate';
@Injectable()

export class MemberDirectorySettingsService implements IMemberDirectorySettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Contacts/MemberDirectory/Settings';
  }

  public Get(request: GetMemberDirectorySettingsRequest): Observable<GetMemberDirectorySettingsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetMemberDirectorySettingsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostMemberDirectorySettingsRequest): Observable<PostMemberDirectorySettingsRequest>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostMemberDirectorySettingsRequest>(apiUrl, request);
  }
}
