import { Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { ActivatedRoute, Router } from '@angular/router';
import { ResponsiveService } from '@tei/core';
import { ChipOptionModel, ChipType } from '@tei/material';
import { Subscription } from 'rxjs';
import { GetMemberDirectoryListRequest, IMemberDirectoryListService, Member } from '../../@intermediate';

@Component({
  selector: 'tei-contacts-member-directory-list',
  templateUrl: './member-directory-list.component.html',
  styleUrls: ['./member-directory-list.component.scss']
})
export class MemberDirectoryListComponent implements OnInit, OnDestroy
{
  form: FormGroup;
  dataSource: Member[] = [];
  getMemberDirectoryListRequest: GetMemberDirectoryListRequest = new GetMemberDirectoryListRequest();
  chipType = ChipType;
  defaultProfilePicture = '/assets/images/ProfilePicture.png';
  metadata: string[] = [];
  allowedColumns: string[] = [];
  itemsCount = 0;
  subscriptions = new Subscription();
  isSmallScreen = false;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  constructor(
    @Inject('IMemberDirectoryListService') private memberDirectoryListService: IMemberDirectoryListService,
    private route: ActivatedRoute,
    private router: Router,
    private responsiveService: ResponsiveService,
  )
  { }

  ngOnInit(): void
  {
    this.subscriptions.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isSmallScreen = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm';
    }));
    // this.isSmallScreen = this.responsiveService.isMobile;
    // Set default request parameters
    this.getMemberDirectoryListRequest.PageSize = 10;
    this.getMemberDirectoryListRequest.PageNumber = 1;
    // Because the filter input components can not accept values from outside, we have to get rid of the filter params.
    if (Object.keys(this.route.snapshot.queryParams).length >= 3)
    {
      this.getMemberDirectoryListRequest.PageSize = +this.route.snapshot.queryParams.PageSize;
      this.getMemberDirectoryListRequest.PageNumber = +this.route.snapshot.queryParams.PageNumber;
      this.updateParams();
    }
    // Subscribe to queryParams and trigger data fetching
    this.subscriptions.add(this.route.queryParams.subscribe(params =>
    {
      if (!params?.PageNumber || !params?.PageSize)
      {
        // Since essential parameters are not provided we ignore all the unessential parameters and construct a URL with needed params.
        this.updateParams();
      } else
      {
        this.fetchMemberDirectoryList();
      }
    }));
  }

  private fetchMemberDirectoryList(): void
  {
    // Fetch members based on current query parameters
    this.memberDirectoryListService.Get(this.route.snapshot.queryParams).subscribe(res =>
    {
      this.itemsCount = res.ItemsCount;
      this.allowedColumns = res.TheColumnsThatHaveViewPermission.map(item => item.ColumnName.toLocaleLowerCase());
      this.metadata = [];
      this.dataSource = [];
      res.TheColumnsThatHaveViewPermission.forEach(perm =>
      {
        if (
          perm.ColumnName.toLowerCase() === "photo" ||
          perm.ColumnName.toLowerCase() === "firstname" ||
          perm.ColumnName.toLowerCase() === "lastname"
        )
        {
          if (!this.metadata.includes('Contact')) this.metadata.push('Contact');
        } else
        {
          this.metadata.push(perm.ColumnName);
        }
      });
      this.dataSource = (res.Members || []).sort((a, b) => a.LastName.localeCompare(b.LastName));
      if (this.dataSource.length === 0 && this.getMemberDirectoryListRequest.PageNumber > 1)
      {
        this.getMemberDirectoryListRequest.PageNumber = 1;
        this.updateParams();
      }
    });
  }

  onSourceChange(source: string, event: ChipOptionModel): void
  {
    // Update request based on chip option selection
    this.getMemberDirectoryListRequest[source] = event.firstValue;

    // Remove property if no value is selected
    if (!event?.firstValue)
    {
      delete this.getMemberDirectoryListRequest[source];
    }
    this.updateParams();
  }

  onPaginatorChange(pageEvent: PageEvent): void
  {
    this.getMemberDirectoryListRequest.PageSize = pageEvent.pageSize;
    this.getMemberDirectoryListRequest.PageNumber = pageEvent.pageIndex + 1;
    this.updateParams();
  }

  private updateParams(): void
  {
    // Update URL parameters
    this.router.navigate([], {
      relativeTo: this.route,
      queryParams: { ...this.getMemberDirectoryListRequest },
      skipLocationChange: false
    });
  }

  hasPermission(key: string): boolean
  {
    return this.allowedColumns.includes(key.toLocaleLowerCase());
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
