import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { GetMemberDirectorySettingsRequest, IMemberDirectorySettingsService, MemberDirectorySettings, PostMemberDirectorySettingsRequest } from '../../@intermediate';

@Component({
  selector: 'tei-contacts-member-directory-settings',
  templateUrl: 'member-directory-settings.component.html',
  styleUrls: ['member-directory-settings.component.scss']
})
export class MemberDirectorySettingsComponent implements OnInit
{
  breadCrumbData: BreadCrumb[];
  form: FormGroup;
  constructor(
    private translate: TranslateService,
    private fb: FormBuilder,
    private snackBarService: SnackBarService,
    @Inject('IMemberDirectorySettingsService') private memberDirectorySettingsService: IMemberDirectorySettingsService,
  )
  {
  }



  ngOnInit(): void
  {
    this.load();
  }

  load()
  {
    this.setBreadCrumbData();

    this.memberDirectorySettingsService.Get(new GetMemberDirectorySettingsRequest()).subscribe(res =>
    {
      this.form = this.fb.group({
        allColumns: [res.AllColumnSettings],// form array with form groups
        selectedColumns: [res.SelectedColumnSettings]
      });
    });
  }

  setBreadCrumbData()
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.SETUP'),
        relativeUrl: '/office/contacts/setup'
      },
      {
        title: this.translate.instant('OFFICE_CONTACTS.WEB_SETUP.WEB_ACCOUNT_CONFIGURATION'),
        relativeUrl: '/office/contacts/setup/web-account-configuration'
      },
      {
        title: this.translate.instant('MEMBER_DIRECTORY.SETTINGS.MEMBER_DIRECTORY'),
        relativeUrl: "",
      },
    ];
  }

  save(form: FormGroup)
  {
    const dto: PostMemberDirectorySettingsRequest = {
      MemberDirectoryColumnIDsStringList: form.value.selectedColumns
    };

    this.memberDirectorySettingsService.Post(dto).subscribe(
      {
        next: () =>
        {
          this.snackBarService.showSuccess(this.translate.instant('MEMBER_DIRECTORY.SETTINGS.SETTINGS_SAVED'), '', null, 'left');
        },
        error: (err) =>
        {
          this.snackBarService.showError(err.message, '', null, 'left');
        }
      }
    );
  }

  get allColumns(): MemberDirectorySettings[]
  {
    return this.form.get('allColumns').value;
  }

  get selectedColumns(): MemberDirectorySettings[]
  {
    return this.form.get('allColumns').value.filter(allCol=>this.form.get('selectedColumns').value.find(f=>f == allCol.ColumnID));
  }

  onColumnRemoved(columnInfo: MemberDirectorySettings): void
  {
    this.form.get('selectedColumns').setValue(this.form.get('selectedColumns').value.filter(f=>f != columnInfo.ColumnID));
  }

}
