import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyRadioModule as MatRadioModule } from "@angular/material/legacy-radio";
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, BreadCrumbModule, FilterChipsModule } from '@tei/material';
import { MemberDirectoryListHeaderModule } from '../../components/member-directory-list-header/member-directory-list-header.module';
import { DefaultModule } from '../default/default.module';
import { PagesModule } from '../pages.module';
import { MemberDirectoryListRoutingModule } from './member-directory-list-routing.module';
import { MemberDirectoryListComponent } from './member-directory-list.component';
import { MemberDirectoryListService } from '../../@services/member-directory-list.service';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule, TeiTextTranslatePipeModule];
const MATERIAL_MODULES = [MatRadioModule, MatDividerModule, MatChipsModule, MatTableModule, MatTooltipModule, MatPaginatorModule];
const TEI_MODULES = [BreadCrumbModule, MemberDirectoryListHeaderModule, AvatarModule, FilterChipsModule];
const MODULES = [
  MemberDirectoryListRoutingModule,
  PagesModule,
  DefaultModule,
  MatFormFieldModule
];

const COMPONENTS = [MemberDirectoryListComponent];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES, ...MATERIAL_MODULES, ...TEI_MODULES],
  exports: [...COMPONENTS],
  declarations: [...COMPONENTS],
  providers: [{ provide: 'IMemberDirectoryListService', useClass: MemberDirectoryListService }]
})
export class MemberDirectoryListModule
{ }
