import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { DefaultModule } from './pages/default/default.module';
import { PagesModule } from './pages/pages.module';
import { MemberDirectoryRoutingModule } from './member-directory-routing.module';
import { MemberDirectoryComponent } from './member-directory.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MODULES = [
  MemberDirectoryRoutingModule,
  PagesModule,
  DefaultModule,
];

const COMPONENTS = [MemberDirectoryComponent];
const PIPES = [];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES],
  exports: [...BASE_MODULES, ...PIPES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: []
})
export class MemberDirectoryModule
{ }
