import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MemberDirectorySettingsComponent } from './member-directory-settings.component';


const routes: Routes = [

  {
    path: '',
    component: MemberDirectorySettingsComponent,
    children: []
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MemberDirectorySettingRoutingModule
{
}
