import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MemberDirectoryListComponent } from './member-directory-list.component';


const routes: Routes = [

  {
    path: '',
    component: MemberDirectoryListComponent,
    children: []
  },
  { path: '**', redirectTo: '' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MemberDirectoryListRoutingModule
{
}
