import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { MemberDirectoryComponent } from './member-directory.component';
import { PagesComponent } from './pages/pages.component';

const routes: Routes = [
  {
    path: '',
    component: MemberDirectoryComponent,
    children: [
      {
        path: '',
        component: PagesComponent,
        children: [
          {
            path: 'settings',
            loadChildren: () => import('./pages/member-directory-settings/member-directory-settings.module').then(m => m.MemberDirectorySettingModule),
            data: {
              RouteData: new RouteData({ Title: 'Member Directory Settings' })
            }
          },
          {
            path: '',
            loadChildren: () => import('./pages/member-directory-list/member-directory-list.module').then(m => m.MemberDirectoryListModule),
            data: {
              RouteData: new RouteData({ Title: 'Member Directory List' })
            }
          },
          { path: '**', redirectTo: '' },
        ]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MemberDirectoryRoutingModule
{
};
