export class GetMemberDirectoryListRequest
{
  PageSize?: number;
  PageNumber?: number;
  City?: string;
  Email?: string;
  FirstName?: string;
  LastName?: string;
  Phone?: string;
  State?: string;
}

export class GetMemberDirectoryListResponse
{
  ItemsCount: number;
  Members: Member[];
  TheColumnsThatHaveViewPermission: TheColumnsThatHaveViewPermission[];
}


export class TheColumnsThatHaveViewPermission
{
  ColumnID: number;
  ColumnName: string;
  ColumnTitle: string;
}

export class Member
{
  City: string;
  Email: string;
  FirstName: string;
  LastName: string;
  Phone: string;
  Photo: string;
  State: string;
}