import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetMemberDirectoryListRequest, GetMemberDirectoryListResponse, IMemberDirectoryListService, PostMemberDirectoryListRequest } from '../@intermediate';
@Injectable()

export class MemberDirectoryListService implements IMemberDirectoryListService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Contacts/MemberDirectory/Members';
  }

  public Get(request: GetMemberDirectoryListRequest): Observable<GetMemberDirectoryListResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetMemberDirectoryListResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostMemberDirectoryListRequest): Observable<PostMemberDirectoryListRequest>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostMemberDirectoryListRequest>(apiUrl, request);
  }
}
