import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyRadioModule as MatRadioModule } from "@angular/material/legacy-radio";
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { MemberDirectorySettingsService } from '../../@services/member-directory-settings.service';
import { DefaultModule } from '../default/default.module';
import { PagesModule } from '../pages.module';
import { MemberDirectorySettingRoutingModule } from './member-directory-settings-routing.module';
import { MemberDirectorySettingsComponent } from './member-directory-settings.component';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule, TeiTextTranslatePipeModule];
const MATERIAL_MODULES = [MatRadioModule, MatDividerModule, MatChipsModule, MatAutocompleteModule, MatSelectModule, MatIconModule];
const TEI_MODULES = [BreadCrumbModule];
const MODULES = [
  MemberDirectorySettingRoutingModule,
  PagesModule,
  DefaultModule,
  MatFormFieldModule
];

const COMPONENTS = [MemberDirectorySettingsComponent];
const PIPES = [];

@NgModule({
  imports: [
    RouterModule.forChild([{path: '', component: MemberDirectorySettingsComponent}]),
    ...BASE_MODULES,
    ...MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES],
  exports: [...COMPONENTS],
  declarations: [...COMPONENTS],
  providers: [{ provide: 'IMemberDirectorySettingsService', useClass: MemberDirectorySettingsService }]
})
export class MemberDirectorySettingModule
{ }
